.map-container{
  width: 100%;
  height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.map{
  width: 70%;
  height: 80%;
  border: none;
  box-shadow: 3px 3px 15px 2px gray;
}

@media(max-width: 1200px){
  .map{
    width: 80%;
    height: 90%
  }
}