footer {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: rgba(128, 128, 128, 0.466);
  height: 10vh;
  font-size: 14px;
  font-family: 'Nunito', sans-serif;
}
.form{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'Nunito', sans-serif;
  font-weight: bold;
  height: 80vh;
  padding-top: 1rem;
}

h1{
  margin: 30px;
}

.note{
  color: rgb(124, 124, 124);
  font-size: 1.5rem;
}

#form {
  display: flex;
  flex-direction: column;
  width: 600px;
  padding: 20px;
}

.field{
  display: flex;
  justify-content: space-between;
}

#form label {
  margin: 10px;
  text-align: left;
}
#form input {
  width: 350px;
  height: 25px;
  border-radius: 10px;
  border: 2px solid lightgray;
  margin: 5px;
  padding: 5px;
  outline: none;
}
#form textarea{
  width: 350px;
  border: 2px solid lightgray;
  border-radius: 10px;
  margin: 5px;
  padding: 5px;
  height: 100px;
  outline: none;
}

.button{
  display: flex;
  justify-content: center;
}

input#button{
  margin-top: 50px;
  height: 35px;
  background-color: rgb(55, 245, 140);
  color: white;
  font-family: 'Raleway', sans-serif;
  border: none;
  font-weight: bold;
  font-size: 1.25rem;
  opacity: 0.8;
  transition: all 0.3s ease
}
#button:hover {
  opacity: 1;
  cursor: pointer;
  -webkit-filter: brightness(0.95);
          filter: brightness(0.95);
}

#button:active {
  transform: scale(.98)
}
h1,
h2,
h3{
  font-family: 'Nunito', sans-serif;
}

h2{
  font-size: 3rem;
}

h3 {
  font-size: 2rem;
}

p {
  font-family: 'Raleway', sans-serif;
  font-size: 1.25rem;
}

@media(max-width: 1200px){
  h1{font-size: 2.75rem;}
  h2{font-size: 2.5rem;}
  h3{font-size: 2rem;}
  p{font-size: 20px;}
}

@media(max-width:600px){
  h1{font-size: 2rem;}
  h2{font-size: 1.75rem;}
  h3{font-size: 1.5rem;}
  p{font-size: 16px;}
}

/* Hero Section */

.home-hero {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 92vh;
  font-size: 2.5rem;
}

.main-hero{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.hero-btn{
  width: 13.5rem;
  height: 3rem;
  border: none;
  outline: none;
  font-family: 'Raleway', sans-serif;
  background-color: rgb(55, 245, 140);
  opacity: 0.8;
  font-size: 20px;
  font-weight: bold;
  border-radius: 10px;
  transition: 0.3s ease;
}

.hero-btn:hover{
  opacity: 1;
  cursor: pointer;
  -webkit-filter: brightness(0.95);
          filter: brightness(0.95);
}
.icon {
  font-size: 60px;
  -webkit-animation: opacity 3s ease infinite;
          animation: opacity 3s ease infinite;
  text-decoration: none;
  color: rgb(26, 245, 234);
}

@-webkit-keyframes opacity {
  0% {
    opacity: 0.3
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0.3;
  }
}

@keyframes opacity {
  0% {
    opacity: 0.3
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0.3;
  }
}

/* About Section */

.about{
  display: flex;
  height: 100vh;
  background: inherit;
  align-items: center;
  justify-content: space-around;
  background-color: rgba(109, 252, 252, 0.315);
}

.about-text{
  width: 50vw;
}

.about-image{
  height: 500px;
  border-radius: 20%;
  box-shadow: 3px 3px 25px 3px rgb(119, 119, 119);
}

@media(max-width:1200px){
  .about{
    flex-direction: column;
    justify-content: center;
  }
  .about-text {
    margin-bottom: 50px;
  }
}

@media(max-width:600px){
  .about-text {
    width: 80vw
  }
  .about-image {
    height:300px
  }
}

/* How to Help Section */

.how-to-help{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.help-items {
  display: flex;
  justify-content: space-around;
  width: 100%;
}


.help-item {
  width: 25vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 2rem;
  border-radius: 10%;
  background-color: rgba(109, 252, 252, 0.315);
  color: black;
}

.help-image{
  width: 60%;
}

@media(max-width: 1200px){
  .how-to-help {
    margin-top: 10px;
  }
  .help-items{
    flex-direction: column;
    align-items: center;
  }
  .help-item {
    width: 50%;
    margin: 10px 
  }
}

@media(max-width:600px){
  .help-item {
    width: 80%;
  }
}

.map-container{
  width: 100%;
  height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.map{
  width: 70%;
  height: 80%;
  border: none;
  box-shadow: 3px 3px 15px 2px gray;
}

@media(max-width: 1200px){
  .map{
    width: 80%;
    height: 90%
  }
}
